:root {
  --primary-color: #156268;
  --primary-dark-color: rgb(17, 80, 85);
  --primary-light-color: #24787e;
  --primary-lighter-color: #28bac5;
  --primary-lightest-color: hsl(184, 66%, 97%);
  --secondary-color: #d35d6e;
  --secondary-dark-color: hsl(351, 57%, 65%);
  --secondary-light-color: hsl(351, 57%, 75%);
  --border-radius: 20px;
  --shadow: 0 0 20px 1px #aaa;
  --dark-shadow: 0 0 20px 1px #333;
  --light-shadow: 0 0 10px 1px #333;
  --label-shadow: 0 0 3px 1px #aaa;
  --zoom-scale: scale(1.005);
  --transition: all 0.3s;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: "Cairo", sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: content-box;
  color: rgb(17, 80, 85);
  color: var(--primary-dark-color);
  background-color: hsl(184, 66%, 97%);
  background-color: var(--primary-lightest-color);
}

.navbar-light .navbar-brand {
  color: #156268;
  color: var(--primary-color);
  font-size: 1.5em;
  font-weight: 600;
}

.navbar-light .navbar-nav .nav-link {
  color: #156268;
  color: var(--primary-color);
  font-size: 1.15em;
  font-weight: 600;
}

.navbar-light li.nav-item {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.navbar-light .navbar-brand,
.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:hover {
  color: rgba(0, 0, 0, 0.9) !important;
  background-color: transparent !important;
}

.main-btn,
.navbar-light .navbar-nav .nav-link.main-btn {
  color: hsl(184, 66%, 97%) !important;
  color: var(--primary-lightest-color) !important;
  background-color: #156268 !important;
  background-color: var(--primary-color) !important;
  border: 1px solid #156268 !important;
  border: 1px solid var(--primary-color) !important;
  transition: all 0.3s;
  transition: var(--transition);
}

.navbar-light .navbar-nav .nav-link.main-btn {
  padding: 0.5em 1em;
}

.main-btn:focus,
.main-btn:hover,
.navbar-light .navbar-nav .nav-link.main-btn:focus,
.navbar-light .navbar-nav .nav-link.main-btn:hover {
  color: #156268 !important;
  color: var(--primary-color) !important;
  background-color: transparent !important;
}

.secondary-btn {
  color: #fff !important;
  background-color: #d35d6e !important;
  background-color: var(--secondary-color) !important;
  border: 1px solid #d35d6e !important;
  border: 1px solid var(--secondary-color) !important;
}

.secondary-btn:hover,
.secondary-btn:focus {
  color: #d35d6e !important;
  color: var(--secondary-color) !important;
  background-color: transparent !important;
}

.user-icon {
  font-size: 1.75em;
  color: #156268;
  color: var(--primary-color);
}
.slick-slider > * {
  height: 100vh;
}

.slideshow-bg {
  height: 100vh;
  background-size: center;
  background-position: center;
  cursor: -webkit-grab;
  cursor: grab;
}

.slideshow-bg:focus {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.slideshow-bg1 {
  background-image: url(/static/media/pexels-photo-3266523.c41429af.jpeg);
}

.slideshow-bg2 {
  background-image: url(/static/media/pexels-photo-1271619.5110da09.jpeg);
}

.slideshow-bg3 {
  background-image: url(/static/media/pexels-photo-59520.c763aa09.jpeg);
}

.slideshow-bg4 {
  background-image: url(/static/media/pexels-photo-2398220.876a1985.jpeg);
}

.card-item {
  width: 100%;
  position: relative;
  border-radius: 20px;
  border-radius: var(--border-radius);
  box-shadow: 0 0 20px 1px #aaa;
  box-shadow: var(--shadow);
  transition: all 0.3s;
  transition: var(--transition);
}

.card-item:hover,
.card-item:focus {
  transform: scale(1.005);
  transform: var(--zoom-scale);
  box-shadow: 0 0 20px 1px #333;
  box-shadow: var(--dark-shadow);
}

.card-item-lg {
  height: 450px;
}

.card-item-sm {
  height: 217px;
}

.card-item-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  border-radius: var(--border-radius);
}

.card-item-highlight {
  height: 240px;
  background-size: cover;
  background-position: center;
  border-top-left-radius: 20px;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: 20px;
  border-top-right-radius: var(--border-radius);
  position: relative;
}

.card-item-name {
  position: absolute;
  left: 5%;
  bottom: 5%;
  color: #fff;
  font-size: 1.5rem;
}

.card-item-type {
  position: absolute;
  right: 0;
  bottom: 5%;
  padding: 0.5em 0.5em 0.5em 1em;
  border-top-left-radius: 1.5em;
  border-bottom-left-radius: 1.5em;
  color: rgb(17, 80, 85);
  color: var(--primary-dark-color);
  font-size: 0.75em;
  background-color: #fff;
  box-shadow: 0 0 3px 1px #aaa;
  box-shadow: var(--label-shadow);
}

.card-item-details {
  position: relative;
  padding: 0.5em 0.75em;
}

.card-item-details h4 {
  font-size: 1.15em;
  color: #156268;
  color: var(--primary-color);
}

.card-item-details .desc {
  width: 75%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: rgb(17, 80, 85);
  color: var(--primary-dark-color);
}

.card-item-details .price,
.card-item-details .rating {
  position: absolute;
  right: 5%;
  color: #28bac5;
  color: var(--primary-lighter-color);
}

.card-item-details .price {
  top: 10%;
}

.card-item-details .rating {
  bottom: 10%;
}

.card-item-details .rating svg {
  margin-bottom: 0.3em;
}

.wishlist-icon {
  position: absolute;
  right: 5%;
  top: 5%;
  font-size: 1.5em;
  color: #fff;
  cursor: pointer;
}

.feature-icon {
  font-size: 0.75em;
  margin-right: 0.25em;
}

footer .nav-link {
  color: #fff;
}

