.buttons-container {
  width: 545px;
  height: 100px;
  border-radius: 45px;
  background: #00171f;
  box-shadow: inset 0 0 2px 2px rgba(0, 0, 0, 0.5);
}
.buttons-container ul {
  list-style: none;
  width: inherit;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.buttons-container ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1px;
  height: 90px;
  width: 105px;
  background: #00171f;
  border-top: 1px solid #353535;
  box-shadow: 0 15px 20px 0 rgba(0, 0, 0, 0.5);
  transition: all 0.5s;
  /* flex-grow: 1; */
}
.buttons-container ul li:nth-of-type(1) {
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
}
.buttons-container ul li:last-of-type {
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
}
.buttons-container ul li:hover {
  cursor: pointer;
}
.buttons-container ul li a {
  display: inline-block;
  color: gray;
  text-decoration: none;
  font-size: 40px;
  transition: all 0.5s;
}
.buttons-container ul li a:hover {
  color: gray;
}
.buttons-container .active {
  background: rgb(1, 53, 70);
  border-top: none;
  border-bottom: 1px solid #252525;
  box-shadow: inset 0 0 10px 1px rgba(0, 0, 0, 0.8);
}
.buttons-container .active-text {
  color: #00171f;
  text-shadow: 0 0 5px #00a8e8;
}
