.outer-circle {
  position: relative;
  height: 140px;
  width: 140px;
  background: linear-gradient(
    var(--primary-color),
    var(--primary-lighter-color),
    var(--primary-color)
  );
  border-radius: 50%;
  /* animation: rotate 1.5s linear infinite; */
}

.outer-circle.first {
  position: absolute;
  top: 45%;
  left: 10%;
}

.outer-circle.second {
  height: 170px;
  width: 170px;
  background: linear-gradient(
    var(--primary-lighter-color),
    var(--primary-color),
    var(--primary-lighter-color)
  );
}

.outer-circle.third {
  position: absolute;
  top: 45%;
  right: 10%;
}

.outer-circle span {
  position: absolute;
  height: 140px;
  width: 140px;
  background: linear-gradient(
    var(--primary-color),
    var(--primary-lighter-color),
    var(--primary-color)
  );
  border-radius: 50%;
}

.outer-circle.second span {
  background: linear-gradient(
    var(--primary-lighter-color),
    var(--primary-color),
    var(--primary-lighter-color)
  );
}

.outer-circle.second span {
  height: 170px;
  width: 170px;
}

.outer-circle.third span {
  /* height: 140px;
    width: 140px; */
}

.circle-span:nth-child(1) {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.circle-span:nth-child(2) {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.circle-span:nth-child(3) {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.circle-span:nth-child(4) {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.inner-circle {
  height: 130px;
  width: 130px;
  position: absolute;
  background: var(--primary-lightest-color);
  top: 5px;
  left: 5px;
  border-radius: 50%;
  z-index: 9;
}

.outer-circle.second .inner-circle {
  height: 160px;
  width: 160px;
}

@keyframes rotate {
  0% {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  100% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

.circle-container {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
}

.circle-container .text {
  text-align: center;
}

.circle-container p {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-weight: 600;
  margin-bottom: 0;
  font-size: 1.4em;
}

.circle-container .value {
  top: 20%;
}

.circle-container .type {
  top: 45%;
  font-size: 2em;
}

.outer-circle.second .circle-container .type {
